import { IProgressBar } from "~/client/Interfaces/GameUIInterface";
import { Graphics, Text, Container } from "pixi.js";
import { getRectangle, getLabel } from "~/client/factories/ViewFactory";
import { RectangleDefinition } from "~/client/types/ViewDefinition";
import { PROGRESS_BAR_AMOUNT_LABEL_STYLE } from "~/client/constants/GameUIConstants";
import { easyReadNumber } from "~/client/utils/StringUtils"

export class ProgressBar extends Container implements IProgressBar{
    private background: Graphics | null;
    private bar: Graphics | null;
    private barMask: Graphics | null;
    private amountLabel: Text | null;

    constructor(private w: number, private h: number) {
        super();

        this.initUI();
    }

    public setProgress(value: number): void {
        if (value > 1 || !this.bar || !this.barMask) return;
        
        if (value <= 0) {
            this.bar.visible = false;
        } else {
            this.barMask.scale.x = value;
            if (!this.bar.visible) this.bar.visible = true;
        }
    }

    public setAmount(value: number): void {
        if (this.amountLabel) this.amountLabel.text = easyReadNumber(value);
    }

    private initUI(): void {
        const style: RectangleDefinition = {
            width: this.w,
            height: this.h,
            lineStyle: {
                width: 2,
                color: 0xFFFF66
            },
            fillStyle: {
                color: 0x1099BB,
                alpha: 1
            }
        }
        this.background = getRectangle(style, 8);
        if (this.background) this.addChild(this.background);

        style.height = this.h - 2;
        style.width = this.w - 2;
        style.lineStyle = null;
        style.fillStyle = {
            color: 0x35CC5A,
                alpha: 1
        }
        this.bar = getRectangle(style, 8);
        if (this.bar) {
            this.bar.x = 1;
            this.bar.y = 1;
            this.addChild(this.bar);
        }
        
        this.barMask = getRectangle(style);
        if (this.barMask && this.bar) {
            this.barMask.x = this.bar.x;
            this.barMask.y = this.bar.y;
            this.addChild(this.barMask);
            this.bar.mask = this.barMask;
        } 

        this.amountLabel = getLabel("", PROGRESS_BAR_AMOUNT_LABEL_STYLE);
        if (this.amountLabel) {
            this.amountLabel.anchor.set(.5, .5);
            this.amountLabel.x = this.w / 2;
            this.amountLabel.y = this.h / 2 + 1;
            this.addChild(this.amountLabel);
        }   
    }
}