import * as PIXI from "pixi.js";
import { MainView } from "~/client/gameViews/mainView";
import { GameUIData, GameStageInfo } from "~/client/types/UIDefinitions";
import { GameData } from "~/client/types/GameDataDefinitions";

export class Main {
    private static readonly GAME_WIDTH = 400;
    private static readonly GAME_HEIGHT = 800;

    private app!: PIXI.Application;
    protected mainView!: MainView;

    constructor() {
        window.onload = (): void => {
            this.startLoadingAssets();
        };
    }

    // add for the test example purpose
    public helloWorld(): string {
        return "hello world";
    }

    private startLoadingAssets(): void {
        const loader = PIXI.Loader.shared;
        loader.add("rabbit", "./assets/rabbit.png");
        loader.add("bg", "./assets/bg.jpg");
        loader.add("arts", "./assets/spritesheet.json");
        loader.add("gameData", "./assets/GameDataDefinition.json");
        loader.add("UIData", "./assets/GameUIDefinition01.json")

        loader.onComplete.once(() => {
            this.onAssetsLoaded();
        });
        //
        loader.load();
    }

    private onAssetsLoaded(): void {
        const dataGame: GameData = PIXI.Loader.shared.resources.gameData.data;
        const dataUI: GameUIData = PIXI.Loader.shared.resources.UIData.data;
        // if (dataUI.stageInfo) this.createRenderer(dataUI.stageInfo);
        if (dataUI.stageInfo) this.createRenderer();

        const stage = this.app.stage;

        const bunny = this.getBunny();
        bunny.position.set(Main.GAME_WIDTH / 2, Main.GAME_HEIGHT / 2);

        // stage.addChild(PIXI.Sprite.from("assets/bg.jpg"));
        this.mainView = new MainView(dataGame, dataUI, this.app.ticker);
        this.app.stage.addChild(this.mainView);
        stage.addChild(bunny);

        this.app.ticker.add(() => {
            bunny.rotation += 0.05;
        });
    }

    private createRenderer(): void {
        this.app = new PIXI.Application({
            backgroundColor: 0xd3d3d3,
            width: Main.GAME_WIDTH,
            height: Main.GAME_HEIGHT,
        });

        document.body.appendChild(this.app.view);

        this.app.renderer.resize(window.innerWidth, window.innerHeight);
        this.app.stage.scale.x = window.innerWidth / Main.GAME_WIDTH;
        this.app.stage.scale.y = window.innerHeight / Main.GAME_HEIGHT;

        window.addEventListener("resize", this.onResize.bind(this));
    }

    private onResize(): void {
        if (!this.app) {
            return;
        }

        this.app.renderer.resize(window.innerWidth, window.innerHeight);
        this.app.stage.scale.x = window.innerWidth / Main.GAME_WIDTH;
        this.app.stage.scale.y = window.innerHeight / Main.GAME_HEIGHT;
    }

    private getBunny(): PIXI.Sprite {
        const bunnyRotationPoint = {
            x: 0.5,
            y: 0.5,
        };

        const bunny = new PIXI.Sprite(PIXI.Texture.from("rabbit"));
        bunny.anchor.set(bunnyRotationPoint.x, bunnyRotationPoint.y);
        // bunny.scale.set(5, 5);

        return bunny;
    }
}

new Main();
